import { Formik, Form } from "formik";
import React from "react";
import {
  Input as TextInput,
  CommonButton,
  UploadInput,
} from "../../../components";
import { Media } from "../../../apiEndPoints";
import validation from "./validation";

function AddBrandForm(mainProps) {
  const { onSubmit, loading, formData } = mainProps;

  const initialValues = {
    image: formData?.image || "",
    name: formData?.name || "",
  };

  return (
    <Formik
      initialValues={{ ...initialValues }}
      validationSchema={validation()}
      onSubmit={onSubmit}
      enableReinitialize
    >
      {(props) => {
        return (
          <Form>
            <div className="form-group">
              <label className="form-label">Brand Name</label>
              <div className="form-control-wrap">
                <TextInput
                  id="name"
                  className="form-control"
                  name="name"
                  disabled={false}
                  variant="standard"
                  type="text"
                  placeholder="Enter brand name"
                  setFieldValue={props.handleChange}
                />
              </div>
            </div>
            <div className="form-group">
              <label className="form-label">Brand Image</label>
              <div className="form-group">
                <div className="upload_photo mb-2 mb-md-3 mx-auto text-center">
                  <div className="img-box">
                    <UploadInput
                      name="image"
                      apiEndPoints={Media.profileImage}
                      type="file"
                      // disabled={!isEdit}
                      defaultImageUrl=""
                      setFieldValue={props.handleChange}
                    >
                      {/* <Tooltip
                        overlayInnerStyle={{ width: "80%" }}
                        placement="right"
                        color="#b9923b"
                        promptText={t("text.common.imageTooltip")}
                      >
                        <label className="mb-0 ripple-effect">
                          <em className="icon ni ni-pen2" />
                        </label>
                      </Tooltip> */}
                    </UploadInput>
                  </div>
                </div>
              </div>
            </div>

            <div className="form-group text-center">
              <CommonButton
                htmlType="submit"
                type="submit"
                loading={loading}
                extraClassName="btn btn-primary ripple-effect"
              >
                {formData?.id ? "Update Brand" : "Add Brand"}
              </CommonButton>
            </div>
          </Form>
        );
      }}
    </Formik>
  );
}

export default AddBrandForm;
