import React, { useEffect, useState } from "react";
import { Card } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import {
  DataTable,
  ListingHeader,
  ModalComponent,
  PageHeader,
  actionFormatter,
  imageFormatter,
} from "../../../components";
import {
  checkValidData,
  decodeQueryData,
  getSortType,
  logger,
  modalNotification,
} from "../../../utils";
import { brandServices } from "../../../services";
import AddBrandForm from "./addBrand";

function Brands() {
  const location = useLocation();
  const { search } = location;
  const [brandData, setBrandData] = useState([]);
  const [param, setParam] = useState({});
  const [page, setPage] = useState(1);
  const [sizePerPage, setSizePerView] = useState(10);
  const [searchName, setSearchName] = useState("");
  const [defaultSort, setDefaultSort] = useState([
    {
      dataField: "",
      order: "",
    },
  ]);
  const [tableLoader, setTableLoader] = useState(false);
  const [noOfPage, setNoOfPage] = useState();
  const [totalCount, setTotalCount] = useState(0);
  const [show, setShow] = useState(false);
  const [rowData, setRowData] = useState("");
  const [loading, setLoading] = useState(false);
  //   const [searchName, setSearchName] = useState("");

  useEffect(() => {
    if (search) {
      const data = decodeQueryData(search);
      setParam(data);
      setPage(data?.page ?? 1);
      if (data?.sortType) {
        const sortData = [
          {
            order: getSortType(data?.sortType),
            dataField: data?.sortBy,
          },
        ];
        setDefaultSort(sortData);
      } else {
        setDefaultSort({
          dataField: "",
          order: "",
        });
      }
    }
  }, [location]);

  const onHandleShow = (row) => {
    setShow(true);
    setRowData(row);
  };

  const getBrandData = async () => {
    setTableLoader(true);
    try {
      let queryParams = {
        offset: (page - 1) * sizePerPage,
        limit: sizePerPage,
        sortBy: param?.sortBy,
        sortType: param?.sortType,
        name: searchName,
      };
      const res = await brandServices.getBrandList(queryParams);
      const { success, data } = res;
      if (success) {
        setBrandData(data.rows);
        setNoOfPage(data.count > 0 ? Math.ceil(data.count / sizePerPage) : 1);
        setTotalCount(data.count);
      }
    } catch (error) {
      logger(error);
    }
    setTableLoader(false);
  };

  //   const headerSortingClasses = (column, sortOrder) => {
  //     return sortOrder === "asc" ? "sorting_asc" : "sorting_desc";
  //   };

  //   const onSortColumn = (field, order) => {
  //     const data = { ...param };
  //     data.sortBy = field;
  //     data.sortType = order === "asc" ? "ASC" : "DESC";
  //     navigateWithParam(data, navigate, pathname);
  //     tableReset();
  //   };

  const options = (row) => {
    const optionsArr = [
      {
        name: "Edit",
        icon: "icon ni ni-pen",
        path: "#addForm",
        action: "confirm",
        onClickHandle: () => {
          onHandleShow(row);
        },
      },
    ];

    return optionsArr;
  };
  const columns = [
    {
      dataField: "name",
      text: "Brand Name",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "thumbnailUrl",
      text: "Brand Thumnails",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell, row) =>
        row?.thumbnailUrl ? imageFormatter(cell) : "-",
    },
    {
      dataField: "BrandBanner",
      text: "Brand Banner",
      //   headerClasses: "sorting",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },
      formatter: (cell) => checkValidData(cell),
    },
    {
      dataField: "action",
      text: "Action",
      headerClasses: "d-flex justify-content-end",
      //   sort: true,
      //   headerSortingClasses,
      //   onSort: (field, order) => {
      //     onSortColumn(field, order);
      //   },

      formatter: (cell, row) => actionFormatter(options(row)),
    },
  ];

  const tableReset = () => {
    setTableLoader(true);
    setBrandData([]);
    setNoOfPage(0);
    setTotalCount(0);
  };

  const getSearchValue = (val) => {
    setSearchName(val);
    if (val) {
      tableReset();
    }
  };

  const onSubmit = async (value) => {
    setLoading(true);
    try {
      let bodydata = {
        ...value,
      };
      const res = rowData
        ? await brandServices?.updateBrand(rowData?.id, bodydata)
        : await brandServices?.addBrand(bodydata);
      if (res?.success) {
        modalNotification({
          type: "success",
          message: res?.message,
        });
        setShow(false);
        setRowData("");
        tableReset();
        getBrandData();
      }
    } catch (error) {
      logger(error);
    }
    setLoading(false);
  };

  useEffect(() => {
    getBrandData();
  }, [param]);
  return (
    <div className="nk-block-head nk-block-head-sm">
      <div className="nk-block-between">
        <Card className="w-100 rounded-2 mb-4">
          <Card.Body>
            <div className="d-flex justify-content-between">
              <PageHeader heading="Brands" />
              <ListingHeader
                btnText="Add Brand"
                onHandleShow={(e) => {
                  e.preventDefault();
                  onHandleShow();
                }}
                btnArray={["create"]}
              />
            </div>
          </Card.Body>
        </Card>
        <ModalComponent
          onHandleCancel={() => {
            setShow(false);
            setRowData("");
          }}
          show={show}
          title={rowData?.id ? "Edit Brand" : "Add Brand"}
        >
          <AddBrandForm
            formData={rowData}
            onSubmit={onSubmit}
            loading={loading}
          />
        </ModalComponent>
      </div>

      <DataTable
        sizePerPagination={false}
        hasLimit
        noOfPage={noOfPage}
        sizePerPage={sizePerPage}
        page={page}
        count={totalCount}
        tableData={brandData}
        tableColumns={columns}
        // param={param}
        defaultSort={defaultSort}
        tableLoader={tableLoader}
        setSizePerPage={setSizePerView}
        tableReset={tableReset}
        getSearchValue={getSearchValue}
        searchPlaceholder="Brand Name"
      />
    </div>
  );
}

export default Brands;
